import React from 'react';

import { tableColumns } from './tableColumns';

import PageTemplate from 'containers/PageTemplate';
import { IDictionaryEvent, THandleGetDictionaryEvents } from 'store';

interface IEvents {
  eventsData: Array<IDictionaryEvent>;
  getEventsData: THandleGetDictionaryEvents;
  isLoading: boolean;
}

export const Events: React.FC<IEvents> = ({
  eventsData,
  getEventsData,
  isLoading,
}) => {
  React.useEffect(
    () => {
      getEventsData();
    },
    [getEventsData]
  );

  return (
    <PageTemplate
      columns={tableColumns}
      data={eventsData}
      isDownloadButton={true}
      isLoading={isLoading}
      title="Events"
    />
  );
};

export default Events;
