import { TableCell, TableHeader } from 'components';
import { IDictionaryEvent } from 'store';
import { ITableCell } from 'types';

type TCell<T extends keyof IDictionaryEvent> = ITableCell<IDictionaryEvent[T]>;

export const tableColumns = [
    {
      maxWidth: 100,
      Header: <TableHeader title="ID" />,
      accessor: 'value',
      Cell: (props: TCell<'value'>) => (
        <TableCell
          value={props.value}
          isNumber={true}
        />
      ),
    },
    {
      maxWidth: 300,
      Header: <TableHeader title="Name" />,
      accessor: 'label',
      Cell: (props: TCell<'label'>) => (
        <TableCell
          value={props.value}
        />
      ),
    },
    {
      maxWidth: 200,
      Header: <TableHeader title="Product Type" />,
      accessor: 'productTypeName',
      Cell: (props: TCell<'productTypeName'>) => (
        <TableCell
          value={props.value}
        />
      ),
    },
  ];
